@import './mixins';
.scan-search-favourite-handle {
  width: 100%;
  height: 100%;
  background: url(../images/scan/ico_star.svg) no-repeat;
  background-size: 100% 100%;
  cursor: pointer;
  @include lightThemeOnly {
    background-image: url(../images/scan/ico_star_light.svg);
  }
  &.scan-search-favourite-handle-active {
    background-image: url(../images/scan/ico_star_active.svg);
    @include lightThemeOnly {
      background-image: url(../images/scan/ico_star_active_light.svg);
    }
  }
}
