@import './var.scss';
@import './theme';
@import './mixins';

#root .base-scan-search {
  position: relative;
  flex: 1;
  font-size: 15px;
  color: rgb(255, 255, 255, 0.7);

  @include lightThemeOnly {
    box-shadow: 0px 0px 20px 0px rgba(16, 12, 29, 0.1);
    border-radius: 10px;
  }

  &.base-scan-search-active {
    .scan-search-result {
      display: block;
    }
  }
  // &:hover {
  //   .scan-search-result {
  //     display: block;
  //   }
  // }

  .ant-input-affix-wrapper {
    padding: 0 20px;
    width: 100%;
    border: 0;
    background: $color-normal-card-bg;
    border-radius: 10px;
    & > input.ant-input {
      border: 0;
      background: $color-normal-card-bg;
      color: $color-text-default;
      height: 40px;
      &::placeholder {
        color: $color-text-default-5;
      }
    }
  }
  .ant-input-disabled {
    background: transparent !important;
  }

  .ant-input-prefix {
    color: $color-text-default;
    margin-right: 10px;

    img {
      width: 20px;
      height: 20px;
    }
  }

  .scan-search-mobile {
    display: none;
  }

  .scan-search-result-holder {
    width: 100%;
    height: 10px;
  }
  .scan-search-result {
    display: none;
    z-index: 30;
    position: absolute;
    top: 50px;
    width: 100%;
    max-height: 450px;
    overflow: auto;
    background: $color-search-bg;
    box-shadow: 0px 0px 8px 0px #28283d;
    border-radius: 10px;
    padding: 10px 0;
    @include lightThemeOnly {
      box-shadow: 0px 0px 20px 0px rgba(16, 12, 29, 0.1);
    }

    .scan-search-table {
      .scan-search-table-thead {
        font-size: 17px;
        font-family: PingFangSC-Semibold, PingFang SC;
        font-weight: 600;
        color: $color-text-default-5;
        line-height: 24px;
      }
      .scan-search-table-tbody {
        color: $color-text-default;
        .scan-search-result-row {
          cursor: pointer;
          &:hover {
            background: rgba(255, 255, 255, 0.05);
          }
          @include lightThemeOnly {
            &:hover {
              background: rgba(51, 51, 51, 0.05);
            }
          }
        }
      }

      .scan-search-result-token-wrap {
        .scan-search-result-row-sub-item {
          color: $color-text-default-7;
          .scan-search-result-row-1 {
            .scan-search-result-version {
              margin-left: 30px;
            }
          }
        }
        .scan-search-result-token-more {
          display: block;
          margin-right: 15px;
          width: 12px;
          height: 12px;
          transition: transform 0.3s;
          user-select: none;
          color: $color-text-default;
        }
      }

      .scan-search-result-row-main-active {
        .scan-search-result-token-more {
          transform: rotate(180deg);
        }
      }

      .scan-search-result-row {
        display: flex;
        padding: 9px 30px;

        .scan-search-result-row-1 {
          display: flex;
          flex-wrap: wrap;
          flex-shrink: 0;
          align-items: center;
          flex: 2;
        }
        .scan-search-result-row-2,
        .scan-search-result-row-3 {
          flex: 1;
        }
      }

      .scan-search-result-tokenSymbol {
        margin-right: 10px;
      }
      .scan-search-result-single {
        display: block;
        margin-right: 10px;
        width: 20px;
        height: 20px;
        border: 1px solid #ddd;
        background: #fff;
        border-radius: 50%;
      }
      .scan-search-result-pool {
        position: relative;
        margin-right: 10px;
        width: 37px;
        height: 20px;
        .scan-search-result-pool-icon {
          display: block;
          width: 20px;
          height: 20px;
          border: 1px solid #ddd;
          background: #fff;
          border-radius: 50%;
          &:nth-of-type(2) {
            z-index: 2;
            position: absolute;
            right: 0;
            top: 0;
          }
        }
        .scan-search-result-pool-icon2 {
          display: block;
          width: 100%;
          height: 100%;
        }
      }
      .scan-search-result-name {
        margin-right: 6px;
      }
      .scan-search-result-version {
        margin-right: 12px;
        padding: 2px 8px;
        line-height: 17px;
        font-size: 12px;
        background: #c1a0ff;
        border-radius: 10px;
      }
      .scan-search-result-v1 {
        background: rgba(193, 160, 255, 0.2);
        color: #d1b9ff;
        @include lightThemeOnly {
          color: #722fff;
        }
      }
      .scan-search-result-v2 {
        background: rgba(255, 104, 104, 0.2);
        color: #ff5858;
      }
      .scan-search-result-v3 {
        background: rgba(255, 178, 97, 0.2);
        color: #ff901c;
      }
      .scan-search-result-stable {
        background: rgba(120, 255, 199, 0.2);
        color: #2be799;
      }
      .scan-search-result-fee {
        margin-right: 15px;
        padding: 2px 8px;
        line-height: 17px;
        font-size: 12px;
        background: $color-text-default-1;
        border-radius: 10px;
        color: $color-text-default-7;
      }
    }

    .scan-earch-result-more {
      padding: 0 30px;
      font-size: 12px;
      font-family: PingFangSC-Regular, PingFang SC;
      font-weight: 400;
      color: $color-button-link-text;
      line-height: 17px;
      .scan-earch-result-more-btn {
        display: inline-flex;
        align-items: center;
        padding: 10px 0;
        cursor: pointer;
        img {
          display: block;
          margin-left: 4px;
          width: 12px;
          height: 12px;
        }
      }
    }

    .scan-earch-result-empty {
      padding: 9px 30px;
      font-size: 15px;
      font-family: PingFangSC-Semibold, PingFang SC;
      font-weight: 600;
      color: $color-text-default;
      line-height: 21px;
    }

    .ant-spin-container::after {
      background: transparent;
    }
  }

  .scan-search-tab {
    display: flex;
    padding: 0 30px;
    margin-bottom: 10px;
    .scan-search-tab-item {
      position: relative;
      margin-right: 24px;
      font-size: 17px;
      font-family: PingFangSC-Semibold, PingFang SC;
      font-weight: 600;
      color: $color-text-default;
      opacity: 0.3;
      line-height: 24px;
      cursor: pointer;
      &:last-child {
        margin-right: 24px;
      }
    }
    .scan-search-tab-item-active {
      opacity: 1;
      &::after {
        position: absolute;
        left: 50%;
        bottom: -8px;
        content: '';
        width: 30px;
        height: 4px;
        background: linear-gradient(135deg, #9017ff 0%, #5a14e1 100%);
        border-radius: 2px;
        transform: translateX(-50%);
      }
    }
  }

  .scan-search-favourite-wrap {
    width: 20px;
    height: 20px;
  }
}

// var(--header-height)
@include mobile-only {
  #root .scan-search-page-notoast {
    .base-scan-search {
      .scan-search-result {
        top: 35px;
        padding: 10px 0;
        width: 100vw;
        left: -39px;
        max-height: calc(100vh - 46px);
      }
    }
  }
  #root .base-scan-search {
    margin-left: 12px;

    .ant-input-affix-wrapper {
      padding: 0 15px;
      height: 26px;
      line-height: 26px;
      background: $color-normal-card-bg;

      & > input.ant-input {
        height: 26px;
        font-size: 12px;
        border-radius: 26px;
      }
    }
    .ant-input-prefix {
      margin-right: 11px;
      img {
        width: 12px;
        height: 12px;
      }
    }

    .scan-search-result {
      top: 35px;
      padding: 10px 0;
      width: 100vw;
      left: -39px;
      max-height: calc(100vh - 86px);
      border-radius: 0;
      .scan-search-result-token-wrap {
        position: relative;
        &::after {
          content: '';
          position: absolute;
          bottom: 0;
          right: 12px;
          left: 12px;
          height: 1px;
          transform: scaleY(0.5);
          transform-origin: 0 0;
          background-color: rgba(255, 255, 255, 0.1);
        }
        &:last-child {
          &::after {
            display: none;
          }
        }
      }

      .scan-search-table {
        .scan-search-result-fee {
          margin-right: 12px;
        }
        .scan-search-result-token-wrap {
          .scan-search-result-token-more {
            margin-right: 12px;
          }
        }
        .scan-search-table-tbody {
          .scan-search-result-row {
            position: relative;
            padding: 12px 12px 4px;
            flex-wrap: wrap;
            flex-shrink: 0;
            &:hover {
              background: transparent;
            }
            &::after {
              content: '';
              position: absolute;
              bottom: 0;
              right: 12px;
              left: 12px;
              height: 1px;
              transform: scaleY(0.5);
              transform-origin: 0 0;
              background-color: rgba(255, 255, 255, 0.1);
            }
            &:last-child {
              &::after {
                display: none;
              }
            }
            .scan-search-result-row-1,
            .scan-search-result-row-2,
            .scan-search-result-row-3 {
              display: flex;
              align-items: center;
              width: 100%;
              flex: auto;
            }

            .scan-search-result-row-2 {
              width: 50%;
              margin: 8px 0;
              font-size: 12px;
            }
            .scan-search-result-row-3 {
              width: 50%;
              font-size: 12px;
            }
            .scan-search-result-row-label {
              font-size: 12px;
              font-family: PingFangSC-Regular, PingFang SC;
              font-weight: 400;
              color: $color-text-default-5;
              line-height: 17px;
            }
          }
        }
      }

      .scan-search-result-row-sub {
        padding: 0 15px;
        .scan-search-result-row-sub-item {
          position: relative;
          margin-bottom: 10px;
          background: rgba(255, 255, 255, 0.05);
          border-radius: 10px;
          overflow: hidden;
          .scan-search-result-row {
            padding: 4px 12px;
            .scan-search-result-row-1 {
              padding-top: 12px;
            }
          }
          .scan-search-result-version {
            position: absolute;
            left: 0;
            top: 0;
            padding: 1px 8px;
            border-radius: 10px 0px 10px 0px;
            margin-left: 0 !important;
          }
        }
      }

      .scan-earch-result-more {
        padding: 0 12px;
        .scan-earch-result-more-btn {
          padding: 4px 0;
        }
      }
      .scan-earch-result-empty {
        padding: 4px 15px;
        font-size: 14px;
        line-height: 20px;
      }
    }
    .scan-search-pc {
      display: none;
    }
    .scan-search-mobile {
      display: block;
    }

    .scan-search-table-thead-m {
      padding: 0 12px;
      font-size: 14px;
      font-family: PingFangSC-Semibold, PingFang SC;
      font-weight: 600;
      color: $color-text-default-5;
      line-height: 20px;
    }

    .scan-search-tab {
      padding: 0 12px;
      .scan-search-tab-item {
        margin-right: 18px;
        font-size: 14px;
      }
      .scan-search-tab-item-active {
        &::after {
          width: 20px;
          bottom: -5px;
        }
      }
    }

    .scan-search-favourite-wrap {
      width: 16px;
      height: 16px;
    }
  }
}
